.card {
  flex-direction: column;
  padding: 32px 16px 16px;
  width: 200px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    height: 134px;
    width: 160px;
    margin: 0 auto 30px;
  }

  &__title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $color-main-accent;
  }

  &__code {
    margin: 0 0 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $color-secondary;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $color-main-accent;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin: 0 0 16px;
    font-size: 12px;
    line-height: 18px;
    color: $color-secondary;
  }

  &__price-number {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $color-main-accent;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    background: $color-accent-blue;
    border: 1px solid transparent;
    border-radius: 5px;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    &__button:hover {
      background: #fff;
      border-color: $color-accent-blue;
      color: $color-accent-blue;
    }
  }
}
